import * as yeoman from 'yeoman-generator';
import * as _ from 'lodash';

export abstract class JsonManager<T> {
  public set(newData: T): T {
    if (!newData) {
      return this._data;
    }
    this._data = newData;
    return this._data;
  }

  public merge(newData: T): T {
    _.merge(this._data, newData);
    return this._data;
  }

  public get data(): T {
    return this._data;
  }

  public save(): void {
    this._fs.writeJSON(this._filepath, this._data, /* replacer */ undefined, /* spacing */ 2);
  }

  protected constructor(protected _filepath: string, protected _fs: yeoman.MemFsEditor, protected _data: T) {}
}